// $nice__color: rgb(23, 255, 205);
@import '../../Variables';

.ulWrap{
    // padding-left: 30px;
    color: rgb(241, 62, 62);
}
.answersItems{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin: 20px 0 30px;
    // max-width: 800px;
}
.answersItem{
    margin: 0 auto;
    // width: 96%;
    width: 100%;
    height: 100%;
    & button{
        text-align: left;
    }
}
.buttonAnswer{
    background: none;
    // border: 1px solid rgb(97, 218, 251);
    border: 1px solid $nice__color;
    width: 100%;
    // margin: 5px auto;
    outline: none;
    padding: 10px 20px;
    border-radius: 5px;
    height: 100%;
    cursor: pointer;
    &:hover{
        // background: rgba(97, 218, 251, 0.1);
        background: $btn__background__color;
      }
}
.question{
    text-align: center;
    font-size: 20px;
}
.questionContainer{
    max-width: 800px;
    margin: 0 auto;
    padding: 10px;
}
.button__redlight{
    background: $redlight__background__color;
    border: 1px solid $redlight__color;
    color: $redlight__color;
    &:hover{
        background: $redlight__background__color;
    }
}
.button__greenlight{
    // background: $greenlight__color;
    // color: $font__dark__color;
    background: $greenlight__background__color;
    border: 1px solid $greenlight__color;
    color: $greenlight__color;
    &:hover{
        // background: $greenlight__color;
        background: $greenlight__background__color;
    }
}