@import '../../Variables';
.answersInput{
    display: grid;
    grid-template-columns: 6fr 2fr;
}
.quizItem{
    background: rgba(0, 0, 0, 0.3);
    margin: 10px 0;
    padding: 15px;
    border-radius: 5px;
}
.question{
    // font-weight: bold;
    // font-size: 17px;
    // margin: 0 0 10px 0;
    margin-bottom: 10px;
    height: 20px;
    text-align: center;
}
.answer{
    margin: 0 0 5px 0;
}
.test{
    background: rgba(0, 0, 0, 0.5);
    margin: 20px 0;
    padding: 15px;
    border-radius: 5px;
    // border: 1px solid rgb(97, 218, 251);
    border: 1px solid $nice__color;

}
.testQ{
    margin-bottom: 10px;
    height: 20px;
    text-align: center;
    
}
.testA{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin: 15px 0 0;
}