@import '../../Variables';

.paragraph{
    padding: 10px;
    font-size: 15px;
    font-weight: bold;
}
.true{
    @extend .paragraph;
    // background: $greenlight__background__color;
    color: $greenlight__color;
}
.true span{
    color: $greenlight__color;
}
.false{
    @extend .paragraph;
    // background: $redlight__background__color;
    color: $redlight__color;
}
.false span{
    color: $redlight__color;
}